.Burger         { position: absolute; top: 0px; left: 0px;  width: 50px; height: 50px; display: none; }
.Burger .Button { position: absolute; top: 14px; left: 15px; }
.BurgerBody     { position: absolute; top: 49px; left: 0px; width: auto; height: calc(100% - 100px);
                  background: var(--BG1); opacity: 1; z-index: 2; display: none; }

.Burger         { -webkit-transition-property: all; transition-property: all; -webkit-transition-duration: .5s; transition-duration: .5s; -webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1); transition-timing-function: cubic-bezier(0, 1, 0.5, 1); }

@media only screen and (max-width: 667px) {
    .Burger     { display: block; width: 100%; height: 100vh; }
    .BurgerBody { display: block; }
}

.Logo           { position: relative; float: left; width: 140px; height: 50px; }
.Logo img       { float: left; width: 100px; height: 50px; }
.Logo .Title    { position: absolute; width: calc( 100% - 77px); top: 11px; left: 57px; margin: 0px; 
                  text-align: left; font-size: 16px; color: #FCFCFC; }
.Logo .SubTitle { position: absolute; top: 29px; left: 57px; text-align: left; font-size: 9px; color: #D8D9D9; }

*           { box-sizing: border-box; margin: 0px; padding: 0px; }
li          { list-style-type: none; }
a, a:hover  { text-decoration: none; }
code        { font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
  --BG0: #FFFFFF;
  --BG1: #AAAAAA;
  --BG2: #BBBBBB;
  --BG3: #FFFFFF;
  --BG9: #505355;
  --BG-GREEN: rgba(0, 201, 196, 0.1);
  --BG-RED: rgba(255, 120, 120, 0.1);

  --FG1: #000000;
  --FG2: #222222;
  --FG3: #444444;
  --BLUE: #5DAAC7;
  --BLUE2: #2C6786;
  --GREEN: #00c9c4;
  --RED: #ff7878;
  --ORANGE: #DF9A57;
}

h1, h2, h3, p {
  float: left;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: #000005;
}

h1 {
  width: 100%;
  margin: 30px auto;
  font-size: 36px;
  font-weight: bold;
  text-align: center;
}

h2 {
  width: 100%;
  margin: 20px auto;
  font-size: 24px;
}

p {
  width: 100%;
  margin: 10px auto;
  font-size: 18px;
}

body {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #FFFFFF;
}

.App {
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 1300px;
  /* min-width: 375px; */
  min-width: 320px;
  /* font-family: 'Roboto Mono', monospace; */
  font-family: 'Montserrat', sans-serif;
  margin: 0px auto;
}

.Section {
  position: relative;
  display: block;
  float: left;
  height: auto;
  width: 100%;
  margin: 20px auto;
  padding: 20px;
}

.MainImage {
  position: relative;
  display: block;
  float: left;
  margin: 0px auto;
  height: auto;
  width: 100%;
  height: auto;
  max-height: 600px;
  object-fit: cover;
}

.Row {
  float: left;
  width: 100%;
  height: auto;
  padding: 20px 20px;
}

.Card {
  float: left;
  width: 300px;
  height: 260px;
  border-radius: 4px;
  margin: 60px 60px;
  padding: 30px 30px;
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
}

.Card .Top {
  float: left;
  width: 100%;
  height: 100px;
}

.Card img {
  float: left;
  width: 70px;
  height: 70px;
}

.Card h3 {
  float: left;
  width: 100%;
  height: 32px;
  font-family: Montserrat;
  font-size: 23px;
  color: #000000;
}

.Card p {
  float: left;
  width: 246px;
  height: 55px;
  font-family: Montserrat;
  font-size: 13px;
  letter-spacing: normal;
  color: #000000;
}

.Nav {
  float: left;
  width: 300px;
  height: 200px;
  border-radius: 4px;
  margin: 40px 40px;
  padding: 30px 30px;
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
}

.Doc {
  float: left;
  width: 300px;
  height: 50px;
  margin: 20px 40px;
}

.Doc a { color: blue; }
.Doc img { float: left; width: 30px; height: 30px; margin-right: 10px; }

.Section1 video {  position: absolute; top: 0px; left: 0px; min-width: 100%; min-height: 100%; object-fit: cover; z-index: -1; }

.Section1 { height: 600px; margin-top: 80px; }
.Section1 h1 {
  float: left;
  width: 320px;
  height: auto;
  margin: 150px 0px 30px 80px;
  font-size: 58px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #ffffff;
  text-align: left;
}

.Section1 h2 {
  width: 320px;
  height: auto;
  margin: 0px 0px 0px 80px;
  font-family: Montserrat;
  font-size: 20px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #ffffff;
  text-align: left;
}

.Section2 { background: #FFFFFF; }
.Section2 h1 span, .Section2 h1 div { float: right; }
.Section2 h2 {
  float: left;
  width: 80%;
  margin: 20px 20px;
  text-align: left;
  font-size: 24px;
  font-weight: 200;
}

.Section3 { height: 300px; }
.Section3 img {  position: absolute; top: 0px; left: 0px; width: 100%; height: 100%; object-fit: cover; z-index: -1; }
.Section3 h1 { margin: 100px 0px 0px 50px; color: #FFFFFF; text-align: left; }


.Section4 img { float: right; width: 360px; }

.Vertical {
  position: absolute;
  top: 200px;
  left: 20px;
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
  -webkit-text-orientation: sideways-right;
          text-orientation: sideways-right;
  color: #FFFFFF;
  font-size: 12px;
  font-style: bold;
}

.Rectangle {
  float: left;
  width: 54px;
  height: 6px;
  margin: 34px 0px 0px 10px;
  background-color: #2779ff;
}

@media only screen and (max-width: 667px) {
}


.Header             { position: relative; float: left; width: 100%; height: 50px; background: var(--BG0); }
.Header .Logo       { width: 100px; }
.HeaderMenu         { position: absolute; top: 5px; left: 200px; width: calc(100% - 320px); height: 100%;  padding: 3px 20px; }
.Header .Profile    { float: right; width: 60px; height: 100%; }


.Header .Status         { float: left; width: 60px; height: 100%; border-radius: 5px; border-radius: 0px; }
.Header .Status span    { display: block; width: auto; margin: 18px auto; font-size: 12px; text-align: center; }
.Header .Status.Online  { color: var(--GREEN1); }
.Header .Status.Offline { color: var(--RED); }

@media only screen and (max-width: 667px) {
    .HeaderMenu     { display: none; }
    .Header .Logo   { position: absolute; top: 0px; left: calc(50% - 50px); }
    .Header .Status { display: none; }
}
